export default function Result(props) {
  return (
    <div className="Result">
      <button>Show Result</button>
      <p>Result</p>
      <style jsx>{`
        button {
          width: 100%;
          background-color: rgb(0, 175, 239);
          color: rgb(255, 255, 255);
          font-size: 16px;
          font-weight: bold;
          text-transform: uppercase;
          padding: 10px;
          border-radius: 4px;
          outline: none;
        }
      `}</style>
    </div>
  )
}